import api from "@/common/index.js";
import empty from "@/components/empty";
let mvc = {
	allList: [],
	activeList: [],
	activeClass: 0,
	navList: [],
	cartoon: false
}
export default {
	components: {
		empty
	},
	data() {
		return {
			mvc: {
				allList: [],
				activeList: [],
				activeClass: 0,
				navList: [],
				cartoon: false
			}
		}
	},
	onLoad(option) {
		let that = this;
		setTimeout(function() {
			mvc=that.mvc
			api.pageTitle(that.$t('voucher').title);
			mvc.navList = that.$t('voucher').navList;
			api.load(that.$t('index').loadmsg);
			that.loadData();
		}, 300)
	},
	computed: {
		i18n() {
			return this.$t('voucher');
		}
	},
	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		// 请求数据
		loadData() {
			let that = this;
			that.cartoon = false;
			that.$ajax.dis().then(res => {
				let list = [];
				let allList = res;
				if (allList.length != 0) {
					allList.forEach(function(item, index) {
						if (item.data.type.expire == 0) {
							if (item.data.type.not_used == 0) {

								list.push(item)
							}
						}

					})
					mvc.activeList = list;
					mvc.allList = res;
				} else {
					mvc.cartoon = true;
				}
				uni.hideLoading();
				that.mvc = mvc;
			})
		},
		//跳转页面
		navCart() {
			uni.switchTab({
				url: '/pages/cart/cart'
			});
		},
		// 商品分类
		changenav(item, index) {
			// 判断not_used,判断未使用和已使用,0是未使用
			// 判断expire,判断是否已过期，0是未过期
			let allList = mvc.allList;

			let listData = [];
			mvc.activeClass = index;
			console.log(index)
			allList.forEach(function(item, j) {
				switch (index) {
					//未过期未使用
					case 0:
						if (item.data.type.expire == 0) {
							if (item.data.type.not_used == 0) {
								listData.push(item);
							}
						}
						break;
						//未过期已使用
					case 1:
						if (item.data.type.not_used == 1) {
							listData.push(item);
						};
						break;
						//已过期
					case 2:
						if (item.data.type.not_used == 0) {
							if (item.data.type.expire == 1) {
								listData.push(item);
							}
						}
				}
			})
			mvc.activeList = listData;
			this.mvc = mvc;
		},
	}
}
